html,body{margin:0;padding:0;border:0;width:100%;height:100%;background-color:#c9c9c9}body{background-image:url("http://www.dovecor.co.za/wp-content/uploads/2015/02/Music-notes.jpg");background-size:cover}.main{display:flex;justify-content:center;flex-direction:column;margin:0 auto}.top-menu{margin-top:200px;display:flex;justify-content:center;align-items:center}.top-menu h1{font-family:'Poiret One', cursive;font-size:50px;letter-spacing:7px}#audio-container{margin-top:50px;padding-bottom:50px;display:flex;justify-content:center;align-items:center}.audio-wrapper{display:flex;box-shadow:0 0 10px #727272, 0 0 40px #727272, 0 0 80px #727272}#audio-note{border:1px solid gray}.note-container{width:1060px;height:750px;border:1px solid gray;background-color:#e7e7e7}.note-container div{display:flex;height:4%}.bottom-menu{position:relative;display:flex;justify-content:space-evenly;width:1200px;height:100px;margin:0 auto;border:1px solid #999999;background-color:#f7f7f7;box-shadow:0 0 5px #b1b1b1, 0 0 10px #b1b1b1, 0 0 15px #b1b1b1}.bottom-menu-left{display:flex;justify-content:space-between;width:200px}.bottom-menu-right{display:flex;justify-content:space-between;align-items:center;font-size:35px}.bottom-menu-right div a{text-decoration:none;color:black;font-family:'Poiret One', cursive;letter-spacing:4px}.bottom-menu-right div{margin-right:40px}.bottom-menu-left button{background-color:transparent;border:none}.bottom-menu-left button i{font-size:35px;cursor:pointer;color:#4d4d4d}.bottom-menu-left button i:hover{font-size:40px;color:#141414;transition:all 0.3s ease}.clear-btn{border:none;background-color:transparent;font-family:'Poiret One', cursive;font-size:30px;letter-spacing:4px;cursor:pointer}.clear-btn:hover{transition:all 0.3s ease;font-size:35px}.tempo-input{font-family:'Poiret One', cursive;font-size:30px;letter-spacing:4px;display:flex;align-items:center}.tempo-input input{height:50px;width:90px;margin-left:10px;font-size:30px;text-align:center;font-family:'Poiret One', cursive;font-weight:600;letter-spacing:4px;background-color:transparent;border:none;outline:none}.github-link,.linkedin-link{position:relative;display:inline-block}.github-link>a:after,.linkedin-link>a:after{content:'';position:absolute;left:0;display:inline-block;height:1em;width:100%;border-bottom:1px solid;margin-top:10px;opacity:0;transition:opacity 0.35s, transform 0.35s;transform:scale(0, 1)}.github-link>a:hover:after,.linkedin-link>a:hover:after{opacity:1;transform:scale(1)}.whitenote{width:100%;height:49.1px;float:left;position:relative;background:white;overflow:visible;box-shadow:-1px 0 0 rgba(255,255,255,0.8) inset,0 0 5px #ccc inset,0 0 3px rgba(0,0,0,0.2);border-bottom:1px solid black;border-radius:0 3px 3px 0}.whitenote:active{box-shadow:2px 0 3px rgba(0,0,0,0.1) inset,-5px 5px 20px rgba(0,0,0,0.2) inset,0 0 3px rgba(0,0,0,0.2);background:linear-gradient(to bottom, #fff 0%, #e9e9e9 100%)}.blacknote{position:absolute;height:55%;width:65%;top:74%;z-index:1;background:#777;border:1px solid #000;border-radius:0 3px 3px 0;box-shadow:-0.5px -0.5px 1.5px rgba(255,255,255,0.2) inset,0 -1.5px 1.5px 2px rgba(0,0,0,0.6) inset,0 1px 2px rgba(0,0,0,0.5)}.blacknote:active{box-shadow:-1px -1px 2px rgba(255,255,255,0.2) inset,0 -2px 2px 3px rgba(0,0,0,0.6) inset,0 1px 2px rgba(0,0,0,0.5);background:linear-gradient(to right, #444 0%, #222 100%)}#piano-roll{width:150px;display:flex;top:50px;flex-direction:column;top:calc(50% - 400px);position:relative}#piano-roll div:first-child{border-radius:3px 3px 0 0}#piano-roll div:last-child{border-radius:0 0 3px 3px}
